<template>
  <div>
    <div id="chart"></div>
  </div>
</template>
<script>
import { Chart } from '@antv/g2'
import { mapState, mapActions } from 'vuex'
export default {
  data() {
    return {
    }
  },
  computed: {
    ...mapState('home', ['accountData']),
    source() {
      if (!this.accountData) {
        return []
      }
      return this.accountData.data.map((item) => {
        return {
          year: `第${item.key}季度`,
          value: Number(item.value)
        }
      })
    }
  },
  watch: {
    accountData() {
      this.init()
    }
  },
  methods: {
    ...mapActions('home', ['getData']),
    init() {
      const chart = new Chart({
        container: 'chart',
        width: 600,
        height: 500,
        autoFit: true,
        padding: 100
      })

      chart.data(this.source)
        // chart.scale({
        //   value: {
        //     min: 0,
        //     formatter: v => {
        //       return v + '元'
        //     }
        //   },
        //   year: {
        //   }
        // })
        chart.tooltip({
          crosshairs: {
            type: 'line'
          }
        });
        chart.axis('value', {
          position: 'left',
          label: {
            formatter: v => {
              return v + '元'
            },
          },
          line: {
            stroke: '#fff'
          }
        })
        chart.line()
          .position('year*value')
          .shape('smooth')
          .label('value', val => {
            return {
              content: val + '元',
              offset: 20
            }
          })
        chart.point()
          .position('year*value')
          .size(4)
          .shape('circle')
          .style({
            stroke: '#fff',
            lineWidth: 1
          });

      chart.render();
    }
  },
  created() {
    this.getData({
      shop_id: sessionStorage.getItem('shop_id') ? Number(sessionStorage.getItem('shop_id')) : undefined
    })
  }
}
</script>
<style>
canvas {
  width: 100% !important;
}
</style>